.wheel{
  position: absolute;
  /* animation: wheel 10s linear -2s infinite; */
  height: 85px;
  position: absolute;
  top: 67%;
  width: 85px;
  z-index: 200;
  /* animation: wheelanimation linear .6s infinite; */
  /* animation: rotation 2s infinite linear; */
}
@keyframes wheel{
  0%{
   transform: rotate(0deg)
  }
  35% {
    transform: rotate(-500deg)
  }
  36%,
  56% {
    transform: rotate(-500deg)
  }
  100%{
    transform: rotate(-1000deg)
  }
}
/* Rotation of the wheels */
@keyframes wheelanimation{
  100% {
      transform: rotate(360deg);
  }
}

#Combined-Shape-roads{
  /* background-repeat: repeat-x; */
  animation: trackanimation 3s linear infinite;
  /* position: absolute; */
  width: 1000vw;
}
/* Moving the track backwards */
@keyframes trackanimation {
  /* 100% {
      transform: translate(-2vw);
  } */
  0% {transform: translate(6%, 0px);}
  100% {transform: translate(-2%, 0px);}
}
.Group-7-sub{
