@keyframes anim15 {
  0% {transform: translate(0px, 0px);}
  100% {transform: translate(-15%, 0px);}
}
@keyframes anim20 {
  0% {transform: translate(0px, 0px);}
  100% {transform: translate(-20%, 0px);}
}
@keyframes anim40 {
  0% {transform: translate(0px, 0px);}
  100% {transform: translate(-40%, 0px);}
}
@keyframes anim2 {
  0% {transform: translate(0px, 0px);}
  100% {transform: translate(-2%, 0px);}
}

.container-fluid{
  padding-right: var(--bs-gutter-x,0px) !important;
  padding-left: var(--bs-gutter-x,0px) !important;
}
@media (prefers-reduced-motion: no-preference) {
